import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as $ from 'jquery';
import * as moment from 'moment';
import * as _ from 'lodash';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { MarketingHttpService } from '../../../http/marketing.http.service';


/**
 * 客户名单预筛选详情
 */
@Component({
	selector: 'client-list-detail',
	templateUrl: './client-list-detail.component.html',
	styleUrls: ['./client-list-detail.component.scss'],
	providers:[MarketingHttpService]
})
export class ClientListDetailComponent implements OnInit {
	@Input() inValue: any;
	msgs:any[];
	tableList:any=[]
	constructor(
		public marketingHttpService:MarketingHttpService
	) {
		
	}

	ngOnInit() {
		let param={
			deId:this.inValue['deId'],
			dedId:this.inValue['dedId']
		}
		this.queryEcho(param);

	}
	//调详情服务
	queryEcho (item) {
		this.marketingHttpService.queryCheckCustomer(item).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.attrvaluelist) {
                    this.tableList = data.attrvaluelist
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
	}
}
