import { Component, OnInit } from '@angular/core';
import { Message, MenuItem } from 'primeng/primeng';
import { QryMarketCampaignInlistBean } from './bean/qryMarketCampaignInlist.bean';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from "app/@uisftech/common/constant/config";
/**
 * 营销活动名单查询
 */
@Component({
	selector: 'app-list-detail',
	templateUrl: './list-detail.component.html',
	styleUrls: ['./list-detail.component.scss'],
	providers: [CusOperationHttpService]
})
export class ListDetailComponent implements OnInit {
	fileList1: any = [];
	fileList2: any = [];
	// private title1: string = '搜索条件';
	title2: string = '客户名单';
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	first: number = 0;//分页控制
	qryMarketCampaignInlistBean: QryMarketCampaignInlistBean = new QryMarketCampaignInlistBean();
	//总页数
	total: number;
	//每页多少条
	pageSize: number = 10;
	list: any = [];//接收后台数据
	totalMessage: any = [];//基本详情的后台数据
	msgs: Message[] = [];//提示信息
	// private intention: any[] = Ispurpose; 
	param: string;
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	contactEquity: any = [];
	code: any;
	seqFlag: any = [];
	marketingProgramId: string;
	trueOrFalse: any = new Array(1);//折叠面板收起、展开
	CampaignSuccess: any = [];//成功与否
	whetherInterested: any = [];//感兴趣与否的下拉值
	sexOpt: any[];//性别（下拉框）
	constructor(
		private httpService: CusOperationHttpService,
		private routeInfo: ActivatedRoute,
		private commfunc: Commfunc,
		private router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.seqFlag = this.code['SEQFlAG'];//营销渠道标识
		this.whetherInterested = this.code['WHETHERINTERESTED'];//客户意图
		this.contactEquity = this.code['CONTACTEQUITY'];//联系情况
		//性别（下拉框）
		this.sexOpt = this.code["SexOpt"];
		// this.CampaignSuccess = this.code['CAMPAIGNSUCCESS'];//营销成功标识
	}

	ngOnInit() {
		/**
		 * 路由传参对应字段
		 */
		if (this.routeInfo.snapshot.params['id']) {
			this.param = this.routeInfo.snapshot.params['id'];
		} else {
			this.param = '';
		}
		this.commfunc.clearSession('flagIndex');
		this.qryMarketCampaignInlistBean.pageSize = 10;
		this.qryMarketCampaignInlistBean.pageNum = 1;
		this.queryDtail();
		this.check();
	}

	//详情
	queryDtail() {
		this.httpService.queryMarketingCampaignById({ marketingCampaignId: this.param }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalMessage = data;
				this.marketingProgramId = data.marketingProgramId;
				this.fileList1 = data.marketingContentFileList;//营销活动说明
				this.fileList2 = data.marketingTargetFileList;//营销目标
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//查看
	check() {
		this.qryMarketCampaignInlistBean.marketingCampaignId = this.param;
		this.httpService.queryCampaignCustList(this.qryMarketCampaignInlistBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.list = data.resultList;
				this.total = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常！' });
			// this.list=[{custName:'1'}]
		})
	}

	//重置
	cleanBtn() {
		this.qryMarketCampaignInlistBean = new QryMarketCampaignInlistBean();
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.qryMarketCampaignInlistBean.pageSize = 10;
		this.qryMarketCampaignInlistBean.pageNum = 1;
		this.first = 0;
	}
	//分页
	paginate(event) {
		//每页显示的条数
		this.qryMarketCampaignInlistBean.pageSize = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.qryMarketCampaignInlistBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.qryMarketCampaignInlistBean.pageSize;
		//调用查询的方法
		this.check();
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//点击页面营销方案中的查看详情按钮跳转营销方案详情页面
	btnDetail() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', { marketingProgramId: this.marketingProgramId }])
	}
	//返回按钮
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
		// let name = '';
		// let path = temp.fileUrl;
		// let u_agent = navigator.userAgent;
		// if (u_agent.indexOf('Chrome') > -1) {
		// 	let donwname = temp.fileName.replace('(', '（');
		// 	name = donwname.replace(')', '）');
		// 	open(TZB_CUS_DOWNLOAD + "?fileName=" + name + "&fileUrl=" + path);
		// } else {
		// 	name = temp.fileName;
		// 	open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
		// }
	}
	compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
