import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as $ from 'jquery';
import * as moment from 'moment';
import * as _ from 'lodash';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { MarketingHttpService } from '../../http/marketing.http.service';


/**
 * 客户名单筛选
 */
@Component({
	selector: 'client-list',
	templateUrl: './client-list.component.html',
	styleUrls: ['./client-list.component.scss'],
	providers: [MarketingHttpService,ConfirmationService]
})
export class ClientListComponent implements OnInit {
	detailDisplay: boolean = false;
	updateDisplay: boolean = false;
	tableHeader: any = [];
	dataList: any = [];
	totalNum: number;
	msgs: any[];
	inValueDetail:any;//详情
	inValueUpdate:any;//修改
	constructor(
		public marketingHttpService: MarketingHttpService,
		public confirmationService:ConfirmationService
	) {

	}

	ngOnInit() {
		this.queryClick()

	}
	//查询
	queryClick() {
		this.marketingHttpService.queryCustomerByTopicId({}).subscribe(data => {//入参后期再加
			if (data.returnCode.code == 'success') {
				this.tableHeader = data.fieldMap;//表头和字段
				this.dataList = data.customerList;//数据
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//删除
	toDelete(item) {
		this.confirmationService.confirm({
            message: '确定要删除吗?',
            accept: () => {
                this.marketingHttpService.delSelectCustomer({}).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
	}
	//详情
	toDetail(item) {
		this.detailDisplay=true;
		this.inValueDetail=item;
	}
	//修改
	toRevamp(item) {
		this.updateDisplay=true;
		this.inValueUpdate=item;
	}
	//修改回调
	outValueUpdate (event) {
		this.updateDisplay=event;
		this.queryClick();
	}

}
