	constructor(
		private routeInfo: ActivatedRoute,
		private httpService: CusOperationHttpService,
		private commfunc: Commfunc,
		public router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.status = this.code['MarketingStatus'];

		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		if (this.user.extParam) {
			this.extParam = this.user.extParam;
			console.log(this.extParam)
		}
		this.item['pageSize'] = 10;
		this.item['pageNum'] = 1;
		// this.marketingCampaignId=this.routeInfo.snapshot.params['marketingCampaignId']
	}
	ngOnInit() {
		this.commfunc.clearSession('flagIndex');
		this.listbtn = { 'id': this.routeInfo.snapshot.params['marketingCampaignId'] };
		// this.param['pageNum'] = 1;
		// this.param['pageSize'] = 10
		this.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];
		this.marktMtatusId = this.routeInfo.snapshot.params['statusId'];
		this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		this.releaseUserId = this.routeInfo.snapshot.params['releaseUserId'];
		if (this.userId == this.releaseUserId && this.marktMtatusId != '4') {//判断当前用户是否可以评价营销活动
			this.pingjiaPage = true;
		} else {
			this.pingjiaPage = false;
		}
		//营销基础信息-详情接口
		let ob = this.httpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId });
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalMessage = data;
				this.marketingProgramId = data.marketingProgramId;
				this.fileList1 = data.marketingContentFileList;//营销活动说明
				this.fileList2 = data.marketingTargetFileList;//营销目标
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常' })
		});
		this.pingJia();
		this.toIntialize();
		// this.init();
	}
	//评价
	pingJia() {
		this.item['marketingCampaignId'] = this.marketingCampaignId;
		//营销活动评价查询
		this.httpService.queryMarketingCampaignEstimate(this.item).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.pingjiaList = data.resultList;
				this.totalCount = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' })
		})
	}

	//初始化-查漏斗式
	toIntialize() {
		let param = {
			marketingCampaignId: this.marketingCampaignId,
		}
		this.httpService.queryMarketingCampaignProcess(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.resultMap) {
					this.items = data.resultMap;
				}
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' })
		})
	}
