	//评价分页
	evaluatePage(event) {
		//每页显示的条数
		this.item['pageSize'] = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.item['pageNum'] = currentPage;
		this.first = (currentPage - 1) * this.item['pageSize'];
		this.pingJia();
		//调用查询的方法
		// this.nodeSelect();
	}
	//提交按钮(营销活动评价)
	sub() {
		if (!this.marketingSummarizeEstimate) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '营销总结评价不能为空！' });
			return;
		}
		let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
		this.userName = chName.userName;
		this.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];
		let param = {
			userName: this.userName,
			userId: this.userId,
			marketingCampaignId: this.marketingCampaignId,
			marketingSummarizeEstimate: this.marketingSummarizeEstimate,
			fileList: this.fileList
		}
		let ob = this.httpService.marketingCampaignEstimate(param);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常' })
		})
		this.pingJia()

	}
	//上传文件
	load(event) {

		for (let file of event.files) {
			this.files.push(file)
		}
		if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
			this.fileList = JSON.parse(event.xhr.response);
		}
		if (JSON.parse(event.xhr.response).success == false) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
			return
		}

		//如果你单个上传 
		/**
		 * map 
		 * 
		 *  this.fileMap=['fileNmme']=this.fileList[0]['fileName'];
		 *  this.fileMap=['fileUrl']= this.fileList[0]['fileUrl'];
		 */
	}
	//点击页面营销方案中的查看详情按钮跳转营销方案详情页面
	btnDetail() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', { marketingProgramId: this.marketingProgramId }])
	}
	//返回按钮
	returnBtn() {
		// setTimeout(() => {
		// 	window.history.go(-1);
		// }, 500)
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-query'])
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
	}
