/**
 * 营销活动查询
 */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { MarketingActivityBean } from './bean/MarketingActivity.bean';
@Component({
	selector: 'marketing-activity-query',
	templateUrl: './marketing-activity-query.component.html',
	styleUrls: ['./marketing-activity-query.component.scss'],
	providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class MarketingActivity implements OnInit {
	marketingActivityBean: MarketingActivityBean = new MarketingActivityBean();
	orgTreeDisplay: boolean = false;
	custMajorName: string;
	personPage: boolean = false;
	msgs: Message[] = []; //提示信息
	personName: string;
	custMajorOrgId: string;
	userInValue: any;
	ch: any = CALENDAR_CN;
	cars: any = [];
	tabMesg: string = tableMessage;
	flage: any;//判断是从方案详情过去的还是从营销活动过去的
	marketingProgramId: string;
	pageNum: number = 1;
	countNum: number = 10;
	totalNum: number;//总条数
	dataList: any = [];
	status: any = [];//营销方案状态
	code: any;
	marketingTimeFrom: Date = null;
	marketingTimeTo: Date = null;
	first: number = 0;//分页控制
	display: boolean = false;
	productList: any = [];//产品树
	productName: string;//产品名称
	productInValue: any;
	ystatus: any = [];
	SearchType: any = [];//查询范围
	seqFlag: any = [];
	userId: string;
	showFlag: boolean;
	paramObj: any = {};
	dealPerShow: boolean = false;//选人
	waitSelUserList: any;
	outVal: any;
	campaign_approveSkipFlag: string;
	postId: string;
	orgId: string;
	campaign_FHapproveSkipFlag: string;
	ownOrgId: string;
	showInfo: string;
	MarketingCampaigntType: any = [];
	selectProductDisplay: boolean = false;//弹出框
	isCatelog: boolean = true;
	managePermission: string;
	stopDisplay: boolean = false; // 中止
	stopReason: string = ''; // 中止原因
	marketingCampaignId: string = '';

	constructor(
		public route: ActivatedRoute,
		public router: Router,
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public confirmationService: ConfirmationService
	) {

		this.marketingProgramId = this.route.snapshot.params['marketingProgramId'];
		// this.marketingProgramId = this.commfunc.getSessionData('marketingProgramId');
		this.orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
		this.ownOrgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.orgId;
		this.managePermission = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission;
		route.queryParams.subscribe(queryParams => {
			this.flage = queryParams.flag;
		});
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.status = this.code['MarketingStatus'];
		this.SearchType = this.code['SEARCHTYPE'];//查询范围
		this.seqFlag = this.code['SEQFlAG'];//营销渠道标识
		this.MarketingCampaigntType = this.code['MarketingCampaigntType'];//任务类型
		this.campaign_approveSkipFlag = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.campaign_approveSkipFlag;//业务主管
		this.campaign_FHapproveSkipFlag = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.campaign_FHapproveSkipFlag;//分行行长
		this.postId = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.postId;
	}

	ngOnInit() {
		this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		this.marketingActivityBean.marketingCampaigntType = this.MarketingCampaigntType[0].value;
		this.marketingActivityBean.countNum = 10;
		this.marketingActivityBean.pageNum = 1;
		this.first = 0;
		if (this.flage) {
			this.echoBtn();
		} else {
			this.marketingActivityBean.countNum = 10;
			this.marketingActivityBean.pageNum = 1;
			this.first = 0;
			this.query()
		}

		if (this.route.snapshot.params['marketingCampaigntType'] && this.route.snapshot.params['marketingCampaigntType'] != '') {
			this.marketingActivityBean.marketingCampaigntType = this.route.snapshot.params['marketingCampaigntType']
		}

	}

	//初始化
	echoBtn() {
		let param = {
			marketingProgramId: this.marketingProgramId,
			pageNum: this.marketingActivityBean.pageNum,
			countNum: this.marketingActivityBean.countNum,
		}
		this.cusOperationHttpService.queryMarketingCampaignByPId(param).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalNum = data.totalCount;
				this.dataList = data.returnList;

			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})

	}

	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.personPage = false;
		this.dealPerShow = false;
	}
	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custMajorName = param.orgName;
		this.marketingActivityBean.releaseOrg = param.orgId;
	}
	//选择员工
	chooseWorker() {
		document.body.style.overflow = 'hidden';
		if (this.marketingActivityBean.releaseOrg == '' || this.marketingActivityBean.releaseOrg == undefined || this.marketingActivityBean.releaseOrg == null) {
			// this.msgs = [];
			// this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择所属机构！' });
			// return
			this.userInValue = '0000';
		} else {
			this.userInValue = this.marketingActivityBean.releaseOrg;
		}
		this.personPage = !this.personPage;

	}

	//员工回调
	perInfor(item) {
		document.body.style.overflow = 'auto';
		this.personPage = false;
		if (item.length > 0) {
			this.marketingActivityBean.userId = item[0].tellerId;
			this.personName = item[0].tellerName;
		}
	}


	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//查询
	query() {
		this.marketingActivityBean.countNum = 10;
		this.marketingActivityBean.pageNum = 1;
		this.first = 0;
		this.queryClick();
	}
	//列表查询
	queryClick() {
		this.marketingActivityBean.marketingTimeFrom = this.commfunc.transDateN(this.marketingTimeFrom);
		this.marketingActivityBean.marketingTimeTo = this.commfunc.transDateN(this.marketingTimeTo);
		this.cusOperationHttpService.queryMarketingCampaignNewList(this.marketingActivityBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalNum = data.totalCount;
				this.dataList = data.returnList;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	//重置
	cleanBtn() {
		this.productName = '';
		this.personName = '';
		this.custMajorName = '';
		this.marketingTimeFrom = null;
		this.marketingTimeTo = null;
		this.marketingActivityBean = new MarketingActivityBean();
		this.marketingActivityBean.marketingCampaigntType = this.MarketingCampaigntType[0].value;
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.marketingActivityBean.countNum = 10;
		this.marketingActivityBean.pageNum = 1;
		this.first = 0;
	}

	product() {
		console.log(11)
		this.selectProductDisplay = true;
		// this.productInValue = { flag: '1' }
	}
	//关联产品和促销手段回调产品
	updateCall(event) {
		this.selectProductDisplay = false;
		this.productList = event;
		let arr = [];
		for (let i of this.productList) {
			let array = {}
			array['productId'] = i.productId;
			array['productName'] = i.productName;
			this.productName = i.productName;
			arr.push(array);
		}
		this.marketingActivityBean.productList = arr;

	}
	paginate　(event) {
		//每页显示的条数
		this.marketingActivityBean.countNum = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.marketingActivityBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.marketingActivityBean.countNum;
		if (this.flage) {
			this.echoBtn();
		} else {
			this.queryClick();
		}
	}
	//开始日期
	showDataStart() {
		if (this.marketingTimeFrom && this.marketingTimeTo) {
			if (this.marketingTimeFrom > this.marketingTimeTo) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.marketingTimeFrom = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.marketingTimeFrom && this.marketingTimeTo) {
			if (this.marketingTimeFrom > this.marketingTimeTo) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.marketingTimeTo = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}

	toDelete(item) {
		if (item.statusId == '1') {//提示信息是删除还是撤销
			this.showInfo = '确认要删除吗?'
		} else {
			this.showInfo = '确认要撤销吗?';
		}
		this.confirmationService.confirm({
			message: this.showInfo,
			accept: () => {
				let param = {
					marketingCampaignId: item.marketingCampaignId,
				}
				this.cusOperationHttpService.removeMarketingCampaignNew(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
						this.queryClick();
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
				})

			}
		});
	}

	//状态是保存的时候是可以进行发布的
	toIssue(item) {
		if (item.marketingCampaigntType == '13' || item.marketingCampaigntType == '16') {//客户营销
			if (this.campaign_approveSkipFlag) {
				this.confirmationService.confirm({
					message: "当前登陆用户岗位为'业务发展部主管',是否跳过审批直接发布活动?",
					accept: () => {
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = item.marketingCampaignId;
						arr['marketingCampaigntType'] = item.marketingCampaigntType;//任务类型
						arr['deId'] = item.deId;//实例编号
						campaign_approveObj['campaign_approveSkipFlag'] = this.campaign_approveSkipFlag,
							this.paramObj = {
								bizType: '02',
								bizPkFieldMap: arr,
								bizFieldMap: campaign_approveObj,
							}
						this.approveClick(this.paramObj);
					},
					reject: () => {//不跳过就选人
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = item.marketingCampaignId;
						arr['marketingCampaigntType'] = item.marketingCampaigntType;//任务类型
						arr['deId'] = item.deId;//实例编号
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
						}
					}
				});
			} else if (this.campaign_FHapproveSkipFlag == 'BELONG_MODE') {
				this.confirmationService.confirm({
					message: "当前登陆用户岗位为'分行行长',是否跳过审批直接发布活动?",
					accept: () => {
						let arr = {};
						let permissionModeObj = {};
						arr['marketingCampaignId'] = item.marketingCampaignId;
						arr['marketingCampaigntType'] = item.marketingCampaigntType;//任务类型
						arr['deId'] = item.deId;//实例编号
						permissionModeObj['campaign_FHapproveSkipFlag'] = this.campaign_FHapproveSkipFlag,
							this.paramObj = {
								bizType: '02',
								bizPkFieldMap: arr,
								bizFieldMap: permissionModeObj,
							}
						this.approveClick(this.paramObj);
					},
					reject: () => {//不跳过就选人
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = item.marketingCampaignId;
						arr['marketingCampaigntType'] = item.marketingCampaigntType;//任务类型
						arr['deId'] = item.deId;//实例编号
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}
						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
						}
					}
				});
			} else {
				this.confirmationService.confirm({
					message: '确定要提交吗?',
					accept: () => {
						let arr = {};
						let campaign_approveObj = {};
						arr['marketingCampaignId'] = item.marketingCampaignId;
						arr['marketingCampaigntType'] = item.marketingCampaigntType;//任务类型
						arr['deId'] = item.deId;//实例编号
						this.paramObj = {
							bizType: '02',
							bizPkFieldMap: arr,
						}
						let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						if (s.extParam.specifyNextOpers) {
							delete s.extParam.specifyNextOpers;
							let commonHeader = s;
							this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						}
						this.waitSelUserList = this.commfunc.query('YXHDFHLC', { postId: this.postId });
						if (!this.waitSelUserList.erro) {
							this.outVal = this.waitSelUserList.waitSelUserList;
							let chooseThis = this.waitSelUserList.display;
							console.log(this.dealPerShow);
							let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
							let a2 = commonHeader.extParam.specifyNextOpers;
							console.log(a2);
							if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
								this.dealPerShow = true;
							} else {
								this.approveClick(this.paramObj);
							}

						} else {
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
						}
					}
				});
			}
		} else {
			this.confirmationService.confirm({//除营销外其他都不走复核
				message: '确定要提交吗?',
				accept: () => {
					let arr = {};
					let campaign_approveObj = {};
					arr['marketingCampaignId'] = item.marketingCampaignId;
					arr['deId'] = item.deId;
					arr['marketingCampaigntType'] = item.marketingCampaigntType;
					this.paramObj = {
						bizType: '03',
						bizPkFieldMap: arr,
					}
					this.approveClick(this.paramObj);
				}
			});
		}

	}

	//发布审批
	approveClick(param) {
		this.cusOperationHttpService.custMarketWorkflowUnifiedEntrance(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.queryClick();
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
		})
	}

	outDisplayCall(event) {
		console.log(event);
		this.dealPerShow = event;
	}
	outValueCall(event) {
		console.log(event);
		this.outVal = event;
		let ss = '';
		this.outVal.forEach(item => {
			ss += item.tellerId + ','
		});
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		commonHeader.extParam['specifyNextOpers'] = ss;
		console.log(commonHeader);
		this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
		this.approveClick(this.paramObj);
	}
	//修改 
	toRevamp(item) {
		this.commfunc.clearSession('items');
		this.commfunc.clearSession('marketingCampaigntType');
		this.commfunc.clearSession('markingURL');
		if (item.marketingCampaigntType == '12') {
			this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/create-marketing-activite/basic-element', item], { queryParams: { flag: '2', expCondNum: '1' } });
		} else {
			this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/create-marketing-activite/basic-element', item], { queryParams: { flag: '2' } });
		}

	}
	//点击查看详情
	toDetail(item) {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-detail', item])
	}
	//营销活动监控
	toMonitor(item) {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/monitor', item])
	}

	typeChange(event) {
		if (event.value == 12) {//交叉销售
			this.status = this.code['MarketingStatus2'];
		} else {
			this.status = this.code['MarketingStatus'];
		}
	}

	// 中止
	toStop(item) {
		this.stopReason = '';
		this.marketingCampaignId = '';
		this.marketingCampaignId = item.marketingCampaignId;
		this.stopDisplay = true;
	}

	// 中止
	stop() {
		if (this.stopReason == '' || this.stopReason == undefined || this.stopReason == null) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请填写中止原因' });
			return
		}
		let param = {
			marketingCampaignId: this.marketingCampaignId,
			stopReason: this.stopReason,
		}
		this.cusOperationHttpService.stopMarketingCampaign(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.queryClick();
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
		})
	}

	// 中止模态框关闭
	close() {
		this.stopReason = null;
		this.stopDisplay = false;
	}

}
